    background: rgb(255, 255, 255);
    position: absolute;
    top: 0;
    left: -100%;
    transition: 0.75s;
  }
}

@media screen and (min-width: 768px) {
  .leftEnd:hover::before,
  .rightEnd:hover::before {
    left: 0;
    z-index: -1;
  }
}

.leftEnd:hover,
.rightEnd:hover {
  color: rgb(51, 51, 51);
  transition: 0.75s;
}
@media screen and (min-width: 768px) {
  .leftEnd {
    margin-right: 1.5rem;
  }
}

@media screen and (min-width: 768px) {
  .rightEnd {
    margin-left: 1.5rem;
  }
}

.current {
  background: rgb(255, 255, 255);
  color: rgb(51, 51, 51);
}